import {getReforge, PREPOPULATE, RARITIES, Reforge} from "./reforges";
import {TalismanCount} from "./main";

export interface Successors {
    bestSuccessors: AccessoryBag[],
    optimal: boolean
}

export interface BaseStats {
    dmg: number,
    str: number,
    cd: number,
    atkSpd: number
}

export class AccessoryBag {
    talismans: any = {};

    public constructor(tc: TalismanCount) {
        tc.count.forEach((count, rarity) => {
            let prepop = PREPOPULATE["Max Damage"][rarity];

            // Typescript is irrationally stupid
            this.talismans[rarity] = [];
            for (let i = 0; i < count; i++) {
                let name = prepop[count % prepop.length];
                this.talismans[rarity].push(getReforge(rarity, name));
            }
        });
    }

    public iterativelyDeepen(baseStates: BaseStats, moves: string[], depthLim: number): Successors {
        let bags = [this];

        for (let gen = 0; gen < depthLim; gen++) {
            let ancestors;
            [bags, ancestors] = [[], bags]; // equiv of mem::replace

            ancestors.forEach(ancestor => {
                ancestor.getPermutations(baseStates, moves).forEach(pm => bags.push(pm));
            });
        }

        return this.orderSuccessors(baseStates, bags);
    }

    getPermutations(baseStats: BaseStats, moves: string[]): AccessoryBag[] {
        let pms = [this];
        for (let rarity in this.talismans) {
            if (!this.talismans.hasOwnProperty(rarity)) {
                continue;
            }

            let pivots = this.scanPivots(rarity);

            pivots.flatMap(pivot => this.talismans[rarity][pivot].shiftWithMoves(moves)
                .map(reforge => {
                    let newBag = this.clone();

                    newBag.talismans[rarity][pivot] = reforge;
                    newBag.fixOrdering();

                    return newBag;
                }))
                .forEach(pm => pms.push(pm));
        }

        return pms;
    }

    public getPositiveSuccessors(baseStats: BaseStats, moves: string[]): Successors {
        let bags = this.getPermutations(baseStats, moves);

        return this.orderSuccessors(baseStats, bags);
    }

    orderSuccessors(baseStats: BaseStats, bags: AccessoryBag[]): Successors {
        bags.sort((a, b) => b.evaluate(baseStats).crit - a.evaluate(baseStats).crit);

        for (let i = 0; i < bags.length; i++) {
            if (bags[i] == this) {
                bags = bags.slice(0, i + 1);
                break;
            }
        }

        return {
            bestSuccessors: bags,
            optimal: bags[0] === this
        }
    }

    scanPivots(rarity: string): number[] {
        let pivots: number[] = [];

        let reforges = this.talismans[rarity];
        if (reforges.length > 0) {
            pivots.push(0);
            let last = reforges[0];

            for (let i = 1; i < reforges.length; i++) {
                if (last != reforges[i]) {
                    last = reforges[i];
                    pivots.push(i);
                }
            }
        }

        return pivots;
    }

    fixOrdering() {
        for (let i in this.talismans) {
            if (!this.talismans.hasOwnProperty(i)) {
                continue;
            }

            let reforges = this.talismans[i];

            // The only requirement for this sorter is that it's deterministic and groups identical reforges together
            // This is made under the assumption that cd \in [0,99], str \in [0,99] and atkspd \in [0, 99]
            reforges.sort((a, b) => (a.cd - b.cd) * 1e4 +
                (a.str - b.str) * 1e2 +
                a.atkspd * b.atkspd);
        }
    }

    clone(): AccessoryBag {
        let newBag = new AccessoryBag(new TalismanCount());

        for (let rarity in this.talismans) {
            if (!this.talismans.hasOwnProperty(rarity)) {
                continue;
            }

            newBag.talismans[rarity] = this.talismans[rarity].slice(0);
        }

        return newBag;
    }

    public evaluate(baseStats: BaseStats): Damage {
        let str = baseStats.str;
        let cd = baseStats.cd;
        let atkSpd = baseStats.atkSpd;

        for (let i in this.talismans) {
            if (!this.talismans.hasOwnProperty(i)) {
                continue;
            }

            this.talismans[i].forEach(reforge => {
                str += reforge.str;
                cd += reforge.cd;
                atkSpd += reforge.atkSpd;
            });
        }

        let std = (5 + baseStats.dmg + Math.floor(str / 5)) * (1 + str / 100);
        let crit = std + std * (cd / 100);

        return {
            std: std,
            crit: crit,
            dps: (2 + 2 * Math.min(1, atkSpd / 100)) * crit
        };
    }

    public toString() {
        return RARITIES.map(rarity => {
            let buffer = rarity + ":\n";
            let collected = countReforgeNames(this.talismans[rarity]
                .map(reforge => reforge.name));

            for (let reforge in collected) {
                if (collected.hasOwnProperty(reforge)) {
                    buffer += "\t" + reforge + ": " + collected[reforge] + "\n";
                }
            }

            return buffer;
        })
            .join("");
    }

    public aggregateReforges(): ReforgeSets[] {
        return RARITIES.map(rarity => {
            return {
                rarity: rarity,
                reforges: countReforgeNames(this.talismans[rarity]
                    .map(reforge => reforge.name))
            };
        });
    }
}

export interface ReforgeSets {
    rarity: string,
    reforges: Map<string, number>
}

function countReforgeNames(names: string[]): Map<string, number> {
    let map = new Map();

    if (names.length > 0) {
        let current = names[0];
        let count = 0;

        for (let name of names) {
            if (name == current) {
                count++;
            } else {
                map[current] = count;

                current = name;
                count = 1;
            }
        }

        map[current] = count;
    }

    return map;

//    return new Map([...map.entries()].sort((a, b) => (a[0] as string) - b[0]));
}

export interface Damage {
    std: number,
    crit: number,
    dps: number,
}