export class Reforge {
    public atkspd: number;

    constructor(public rarity: string, public name: string, public str: number, public cd: number, atkSpd?: number) {
        this.atkspd = atkSpd === undefined ? 0 : atkSpd;
    }

    public shiftWithMoves(moves: string[]): Reforge[] {
        return moves
            .filter(name => name != this.name)
            .filter(name => REFORGES[this.rarity]
                .filter(reforge => reforge.name === name)
                .length > 0
            )
            .map(name => getReforge(this.rarity, name));
    }

    public shift(moveSet: string): Reforge[] {
        let moves = MOVE_SETS[moveSet]

        return this.shiftWithMoves(moves);
    }
}

export const MOVE_SETS = ["Hurtful", "Strong", "Forceful"];

export const PREPOPULATE = {
    "Max Damage": {
        "COMMON": ["Hurtful"],
        "UNCOMMON": ["Hurtful"],
        "RARE": ["Hurtful"],
        "EPIC": ["Strong"],
        "LEGENDARY": ["Strong"],
        "MYTHIC": ["Strong"],
    }
};

export const RARITIES = ["COMMON", "UNCOMMON", "RARE", "EPIC", "LEGENDARY", "MYTHIC"];

