import {AccessoryBag, BaseStats, ReforgeSets} from "./accessoryBag";
import {MOVE_SETS, RARITIES} from "./reforges";

export class TalismanCount {
    public count: Map<string, number> = new Map();
}

document.addEventListener("DOMContentLoaded", function () {
    for (let rarity of RARITIES) {
        listenTo(rarity.toLowerCase() + "-talisman-count");
    }

    for (let stat of ["dmg", "strength", "crit-dmg", "attack-speed"]) {
        listenTo("base-" + stat);
    }

    for (let reforge of ["silky"]) {
        listenTo("reforge-stone-" + reforge);
    }

    updateMaxCD();
});

function listenTo(inputId: string) {
    let el: HTMLInputElement = document.getElementById(inputId) as HTMLInputElement;

    el.addEventListener("change", e => updateMaxCD());
}

function updateMaxCD() {
    let [bag, optimal] = evaluateMaxCD();

    for (let reforgeSet of optimal) {
        let container = document.getElementById(reforgeSet.rarity.toLowerCase() + "-max-cd-reforge-guide");
        container.innerText = "";

        for (let reforge in reforgeSet.reforges) {
            if (!reforgeSet.reforges.hasOwnProperty(reforge)) {
                continue;
            }

            let count = reforgeSet.reforges[reforge];

            let reforgeEl = document.createElement("div");
            reforgeEl.innerText = reforge + ": " + count;

            container.appendChild(reforgeEl);
        }
    }
}

function evaluateMaxCD(): [AccessoryBag, ReforgeSets[]] {
    let moves = MOVE_SETS.slice(0);
    if ((document.getElementById("reforge-stone-silky") as HTMLInputElement).checked) {
        moves.push("Silky");
    }

    let bag = new AccessoryBag(getTalismanCount());
    let bs = getBaseStats();

    for (let i = 0; i < 500; i++) {
        let successors = bag.iterativelyDeepen(bs, moves, 2);

        console.info("Generation " + i + " results:\n\n" + successors.bestSuccessors[0].toString());
        bag = successors.bestSuccessors[Math.floor(Math.random() * successors.bestSuccessors.length)];

        if (successors.optimal) {
            console.info("Optimal solution found after " + i + " generations.");
            return [bag, bag.aggregateReforges()];
        }
    }

    console.error("Took too long to compute, interrupting...");
    return undefined;
}

function getTalismanCount(): TalismanCount {
    let count = new TalismanCount();

    for (let rarity of RARITIES) {
        let id = rarity.toLowerCase() + "-talisman-count";
        let input = getValueAsUint(id);

        if (input > 50) {
            input = 50;
            (document.getElementById(id) as HTMLInputElement).value = "50";
        }

        count.count.set(rarity, input)
    }

    return count;
}

function getValueAsUint(elementId: string): number {
    let el = document.getElementById(elementId) as HTMLInputElement;
    let v = el.value as unknown as number;

    return v > 0 ? +v : 0;
}

function getBaseStats(): BaseStats {
    return {
        dmg: getValueAsUint("base-dmg"),
        str: getValueAsUint("base-strength"),
        cd: getValueAsUint("base-crit-dmg"),
        atkSpd: getValueAsUint("base-attack-speed")
    };
}
